<script>

const axios = require('axios');

export default {
  name: 'explorer',
    components: {
  },
  data() {
      return {
          data : [ 
           []
          ],
          totalPages : 0,
          currentPage: 1,
          entryPerPage : 5,
          totalUser : 0,
          dayUser : 0
      }
  },
  mounted() {
    let self = this;
      (async()=> {
        let result = await axios.get('http://52.66.159.189:8081/chainusers/dashboarddata')
        self.totalUser = result.data.count
        self.dayUser = result.data.lastDay
        if(self.totalUser % self.entryPerPage ){
          self.totalPages = Math.floor(self.totalUser / self.entryPerPage ) + 1
        } else {
          self.totalPages = Math.floor(self.totalUser / self.entryPerPage )
        }
        let data = []
        let index = 0
        while(index < self.totalUser){
          data.push(result.data.users.slice(index, index + self.entryPerPage))
          index += self.entryPerPage
        }
        self.data = data
      })()
  },
  methods : {
    nextPage() {
      if(this.currentPage < this.totalPages) {
        this.currentPage = this.currentPage + 1
      }
    },
    prevPage () {
      if( this.currentPage > 1 ) {
        this.currentPage = this.currentPage - 1
      }
    }
  }
};
</script>

