<template>
  <div class="register">
     <main>
    <section class="section section-shaped section-lg">
      <div class="shape shape-style-1 bg-gradient-default">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div class="container pt-lg-md">
        <div class="row justify-content-center">
          <div class="col-lg-5">
            <div class="card bg-secondary shadow border-0">
              <div class="card-body px-lg-5 py-lg-5">
                <div class="text-center text-muted mb-4">
                  <small>Create your chain ID now</small>
                </div>
                <form>
                  <div class="form-group mb-3">
                    <div class="input-group input-group-alternative">
                      <div class="input-group-prepend">
                        <span class="input-group-text"><i class="ni ni-email-83"></i></span>
                      </div>
                      <input class="form-control" placeholder="Username" type="text" v-model="username">
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group input-group-alternative">
                      <div class="input-group-prepend">
                        <span class="input-group-text"><i class="ni ni-lock-circle-open"></i></span>
                      </div>
                      <input class="form-control" placeholder="Password" type="password" v-model="password">
                    </div>
                  </div>
                  <div class="text-center">
                    <button type="button" class="btn btn-primary my-4" @click="login">Create ID</button>
                  </div>
                </form>
              <div v-if="warn">
                <p class="text-warning">{{ msg }}</p>
              </div>
              </div>
            </div>
            <div class="row mt-3">
              <!-- <div class="col-6">
                <a href="#" class="text-light"><small>Forgot password?</small></a>
              </div>
              <div class="col-6 text-right">
                <a href="#" class="text-light"><small>Create new account</small></a>
              </div> -->
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
  </div>
</template>
<script>
const axios = require('axios');
import Router from '@/router';
import EventBus from '../eventBus'

export default {
  data: () => {
    return {
      username : '',
      password : '',
      warn : false,
      msg : 'Username already exist'
    }
  },
  mounted() {
    if(localStorage.loggedIn == "true") {
      Router.push('profile') 
    }
  },
  methods : {
    login () {
      let self = this;
      if(self.password.length < 6) {
          self.msg = "Password too short (min 6 letters)"
          self.warn = true
      }
      else {
        (async()=> {
          let result = await axios.post('http://52.66.159.189:8081/chainusers/register', {
            username: self.username,
            password: self.password,
            name : "demo"
          })
          if(result.data.message == "User created") {
            self.warn = false
            localStorage.username = self.username
            localStorage.password = self.password
            localStorage.loggedIn = true
            EventBus.$emit('login', {})
            Router.push('profile')  
          }
          else if( result.data.message == "User already registered" ) { 
            self.msg = "Username already exists"
            localStorage.loggedIn = false
            self.warn = true
          }
          else {
            self.msg = "something went wrong"
            localStorage.loggedIn = false
            self.warn = true 
          }     
        })()
      } 
    }
  }
}
</script>