<script>
const axios = require('axios');
import Router from '@/router';
import EventBus from '../eventBus'


export default {
  data : () => {
    return {
      username: '',
      password : '',
      warn : false,
      msg : ''
    }
  },
  mounted() {
    console.log(localStorage)
  },
  methods : {
    login () {
      let self = this;
      (async()=> {
        try {
          let result = await axios.post('http://52.66.159.189:8081/chainusers/login', {
          username: self.username,
          password: self.password
          })
          console.log(result)
          if(result.data.authenticated) {
            self.warn = false
            localStorage.username = self.username
            localStorage.password = self.password
            localStorage.loggedIn = true
            EventBus.$emit('login', {})
            Router.push('profile')
          }
          else if (result.data.message == "Logged in successfully") {
            self.msg = "invalid username/password"
            localStorage.loggedIn = false
            self.warn = true  
          }
          else {
            self.msg = "something went wrong"
            localStorage.loggedIn = false
            self.warn = true 
          }
        }
        catch(err) {
            self.msg = "invalid username/password"
            localStorage.loggedIn = false
            self.warn = true 
        }
      })()
    }
  }
}
</script>